.gpt3__navbar{
    /* background-color: rgb(74, 121, 162); */
    /* border: 2px solid red; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;

}
.scale-up-tr {
	-webkit-animation: scale-up-tr 0.4s cubic-bezier(0.550, 0.055, 0.675, 0.190) both;
	        animation: scale-up-tr 0.4s cubic-bezier(0.550, 0.055, 0.675, 0.190) both;
}
.gpt3__navbar-links{
   /* background-color: aqua; */
    /* border: 2px solid brown; */
    flex: 1;
    display: flex;
    /* justify-content: flex-start; */
    align-items: center;
}
.gpt3__navbar-links_logo{
    margin-right: 2rem; 
}
.gpt3__navbar-links_logo img{
    width: 62.56px;
    height: 16.02px;
}
.gpt3__navbar-links_container{
    display: flex;
    flex-direction: row; 
}
.gpt3__navbar-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex: inherit;
}
.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 17px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}
.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button{
     padding: 0.5rem 1rem ;
     color: #fff;
     background: #ff4820;
     font-family: var(--font-family);
     font-size: 18px;
     line-height: 25px ;
     font-weight: 500;
     border-radius: 5px;
     border: 0;
     outline: none ;
     cursor: pointer;
}
.gpt3__navbar-menu{
    display: none;
    margin-left: 1rem;
    /* display: none; */
    position: relative;
}
.gpt3__navbar-menu svg{
    cursor: pointer;
}
.gpt3__navbar-menu-container{
    /* border: 2px solid rgb(221, 213, 213); */
    color: #fff;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}
.gpt3__navbar-menu-container p{
    margin: 1rem 0;
}
.gpt3__navbar-menu_container-links-sign{
    display: none;
}
@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container{
        display: none;
    }
    .gpt3__navbar-menu{
        display: flex;
    }
}
@media screen and (max-width: 700px) {

    .gpt3__navbar{
        padding: 2rem 4rem;
    }
}
@media screen and (max-width: 550px) {

    .gpt3__navbar{
        padding: 2rem;
    }
    .gpt3__navbar-sign{
        display: none;
    }
    .gpt3__navbar-menu-container{
        top: 20px;
    }
    .gpt3__navbar-menu_container-links-sign{
        display: block;
    }
    .gpt3__navbar-links{
        display: flex;
        justify-content: space-between;
    }
}